<template>
  <div class="page-container" :class="{'centered': !isMobile, 'parallex': isMobile}" :style="{backgroundImage: `url(${bgImages})`}">
    <desktop-layout v-if="!isMobile"/>
    <mobile-layout v-else/>
  </div>
</template>

<script>
import pageData from '@/assets/data'
import DesktopLayout from './Desktop'
import MobileLayout from './Mobile'
export default {
  name: 'HomePage',
  components: {
    DesktopLayout,
    MobileLayout
  },
  data () {
    return {
      isMobile: false,
      bgImages: null
    }
  },
  mounted () {
    const bgType = this.$route.query.bg_type        
    let bgSrc = null
    if(!bgType) {
      bgSrc = require('@/assets/images/bg-page.jpg')
    }else if(bgType == 2) {
      bgSrc = require('@/assets/images/bg-page2.jpg')
    }else if(bgType == 3) {
      bgSrc = require('@/assets/images/bg-page3.jpg')
    }else if(bgType == 4) {
      bgSrc = require('@/assets/images/bg-page4.jpg')
    }
    this.bgImages = bgSrc
    this.isMobile = window.screen.width < 768
    window.addEventListener("resize", this.screenResizeListener)

  },
  destroy () {
    window.addEventListener("resize", this.screenResizeListener)
  },
  methods: {
    screenResizeListener (e) {
      this.isMobile = window.screen.width < 768
    }
  }
}
</script>
<style lang="scss" scoped>
.page-container{  
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  &.centered{
    display: flex;
    align-items: center;
    justify-content: center; 
  }   
  &.parallex{
    background-attachment: fixed;
    height: auto;
  }    
}

</style>


